import { getArticle,updateArticle,createArticle } from "@/api/article.js";
export default {
  middleware: ["authenticated"],
  name: "EditorArticle",
  data() {
    return {
      newTag:'',
      publishDisabled:false,
      article: {
        title: "",
        description: "",
        body: "",
        tagList: [],
      },
    };
  },
  components: {},
  mounted() {
    this.getArticle();
  },
  computed:{
    slug(){
      return  this.$route.params.slug
    }
  },
  methods: {
    async getArticle() {
      try {
        if (!this.slug) return;
        const { data } = await getArticle(this.slug);
        this.article = data.article
      } catch (err) {
        console.log(err);
      }
    },
    enterTag(){
      this.article.tagList.push(this.newTag)
      this.newTag = ''
    },
    removeTag(i){
      this.article.tagList.splice(i,1)
    },
    async submitArticle () {
      try{
        this.publishDisabled=true
        const {data} = this.slug? await updateArticle(this.slug,{article: this.article}): await createArticle({article: this.article})
        if(data.article){
          this.article = data.article
          this.$router.push({
            name:'article',
            params:{
              slug:this.article.slug
            }
          })
        }else{
          console.log('出错了')
        }
        this.publishDisabled=false
      }catch(err){
        console.log(err)
      }
    }
  },
};
